import React from 'react';
import { useLocation } from 'react-router-dom';
import { useAuth } from '../../../contexts/AuthContext';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiSearch, FiUserPlus, FiUser, FiLogOut } = FiIcons;

const TopBar = () => {
  const location = useLocation();
  const { user, logout } = useAuth();

  const getPageTitle = () => {
    switch (location.pathname) {
      case '/': return 'Overview';
      case '/users': return 'Users';
      case '/licences': return 'Licences';
      case '/billing': return 'Billing / Global Pool';
      case '/api-config': return 'API Configuration';
      case '/quota-logs': return 'Quota & Logs';
      case '/audit-alerts': return 'Audit & Alerts';
      case '/settings': return 'Settings';
      default: return 'Dashboard';
    }
  };

  return (
    <header className="bg-app-card border-b border-gray-700 px-6 py-4">
      <div className="flex items-center justify-between">
        <h1 className="text-xl font-semibold text-app-text-primary">
          {getPageTitle()}
        </h1>

        <div className="flex items-center space-x-4">
          <div className="relative">
            <SafeIcon icon={FiSearch} className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-app-text-secondary" />
            <input
              type="text"
              placeholder="Global search..."
              className="pl-10 pr-4 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent w-64"
            />
          </div>

          <button className="flex items-center px-4 py-2 bg-app-primary text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200">
            <SafeIcon icon={FiUserPlus} className="w-4 h-4 mr-2" />
            Add User
          </button>

          <div className="flex items-center space-x-2">
            <div className="flex items-center space-x-2 px-3 py-2 bg-app-card-deep rounded-lg">
              <SafeIcon icon={FiUser} className="w-4 h-4 text-app-text-secondary" />
              <span className="text-sm text-app-text-primary">{user?.email}</span>
            </div>
            
            <button 
              onClick={logout}
              className="p-2 text-app-text-secondary hover:text-app-danger rounded-lg hover:bg-app-card-deep"
            >
              <SafeIcon icon={FiLogOut} className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>
    </header>
  );
};

export default TopBar;