import React, { useState } from 'react';
import { motion } from 'framer-motion';
import AdvancedSettingsModal from '../Modals/AdvancedSettingsModal';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiSettings } = FiIcons;

const OptionsCard = () => {
  const [loop, setLoop] = useState(false);
  const [autoSplit, setAutoSplit] = useState(false);
  const [delimiters, setDelimiters] = useState('.,;!?');
  const [showAdvanced, setShowAdvanced] = useState(false);

  return (
    <>
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card hover:shadow-card-hover transition-shadow"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.3 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-4">Options</h3>
        
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <label className="text-sm text-app-text-primary">Loop</label>
            <input
              type="checkbox"
              checked={loop}
              onChange={(e) => setLoop(e.target.checked)}
              className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2"
            />
          </div>

          <div className="flex items-center justify-between">
            <label className="text-sm text-app-text-primary">Auto Split</label>
            <input
              type="checkbox"
              checked={autoSplit}
              onChange={(e) => setAutoSplit(e.target.checked)}
              className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2"
            />
          </div>

          {autoSplit && (
            <div>
              <label className="block text-sm text-app-text-primary mb-2">Delimiters</label>
              <input
                type="text"
                value={delimiters}
                onChange={(e) => setDelimiters(e.target.value)}
                className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
                placeholder=".,;!?"
              />
            </div>
          )}

          <button
            onClick={() => setShowAdvanced(true)}
            className="w-full bg-app-violet text-white py-2 px-4 rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 flex items-center justify-center"
          >
            <SafeIcon icon={FiSettings} className="w-4 h-4 mr-2" />
            Advanced Settings
          </button>
        </div>
      </motion.div>

      <AdvancedSettingsModal 
        isOpen={showAdvanced} 
        onClose={() => setShowAdvanced(false)}
      />
    </>
  );
};

export default OptionsCard;