import React from 'react';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiInbox } = FiIcons;

const DataTable = ({ title, columns, data, emptyMessage = "No data available" }) => {
  return (
    <div className="bg-app-card rounded-card p-6 shadow-card">
      <h3 className="text-lg font-semibold text-app-text-primary mb-4">{title}</h3>
      
      {data.length === 0 ? (
        <div className="text-center py-12 text-app-text-secondary">
          <SafeIcon icon={FiInbox} className="w-12 h-12 mx-auto mb-4 opacity-50" />
          <p>{emptyMessage}</p>
        </div>
      ) : (
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                {columns.map((column) => (
                  <th key={column.key} className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">
                    {column.label}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              {data.map((row, index) => (
                <tr key={index} className="border-b border-gray-700 hover:bg-app-card-deep">
                  {columns.map((column) => (
                    <td key={column.key} className="py-3 px-2 text-sm text-app-text-primary">
                      {column.render ? column.render(row[column.key]) : row[column.key]}
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default DataTable;