import React from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import SafeIcon from '../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiLoader } = FiIcons;

const ProtectedRoute = ({ children, requireAdmin = false }) => {
  const { user, loading } = useAuth();

  if (loading) {
    return (
      <div className="min-h-screen bg-app-bg flex items-center justify-center">
        <SafeIcon icon={FiLoader} className="w-8 h-8 text-app-primary animate-spin" />
      </div>
    );
  }

  if (!user) {
    return <Navigate to="/login" replace />;
  }

  if (requireAdmin && user.role !== 'ADMIN') {
    return <Navigate to="/login" replace />;
  }

  if (!user.enabled || !user.licence?.active) {
    return (
      <div className="min-h-screen bg-app-bg flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-app-danger mb-2">Access Denied</h1>
          <p className="text-app-text-secondary">Your account or licence is not active.</p>
        </div>
      </div>
    );
  }

  return children;
};

export default ProtectedRoute;