import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useAdmin } from '../../../contexts/AdminContext';
import SafeIcon from '../../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiSearch, FiFilter, FiUserPlus, FiMoreVertical, FiEdit, FiTrash2, FiEye, FiKey } = FiIcons;

const Users = () => {
  const { users, addUser, updateUser, deleteUser } = useAdmin();
  const [searchTerm, setSearchTerm] = useState('');
  const [showAddUser, setShowAddUser] = useState(false);
  const [editingUser, setEditingUser] = useState(null);

  const filteredUsers = users.filter(user =>
    user.email.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleDeleteUser = (userId) => {
    const confirmed = window.confirm('Type DELETE to confirm deletion');
    if (confirmed) {
      deleteUser(userId);
      toast.success('User deleted successfully');
    }
  };

  const toggleUserEnabled = (userId, currentStatus) => {
    updateUser(userId, { enabled: !currentStatus });
    toast.success(`User ${!currentStatus ? 'enabled' : 'disabled'}`);
  };

  return (
    <div className="space-y-6">
      {/* Toolbar */}
      <div className="bg-app-card rounded-card p-6 shadow-card">
        <div className="flex flex-wrap items-center justify-between gap-4">
          <div className="flex items-center space-x-4">
            <div className="relative">
              <SafeIcon icon={FiSearch} className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-app-text-secondary" />
              <input
                type="text"
                placeholder="Search by email..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 pr-4 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent w-64"
              />
            </div>
            <button className="flex items-center px-4 py-2 bg-app-card-deep border border-gray-600 text-app-text-primary rounded-lg hover:bg-gray-600 transition-colors">
              <SafeIcon icon={FiFilter} className="w-4 h-4 mr-2" />
              Filters
            </button>
          </div>
          
          <button 
            onClick={() => setShowAddUser(true)}
            className="flex items-center px-4 py-2 bg-gradient-to-r from-app-primary to-app-primary-end text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
          >
            <SafeIcon icon={FiUserPlus} className="w-4 h-4 mr-2" />
            Add User
          </button>
        </div>
      </div>

      {/* Users Table */}
      <div className="bg-app-card rounded-card p-6 shadow-card">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">
                  <input type="checkbox" className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2" />
                </th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Email</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Role</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Enabled</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Credits</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Expiry</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Created</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Last Login</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredUsers.map((user) => (
                <motion.tr 
                  key={user.id}
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="border-b border-gray-700 hover:bg-app-card-deep"
                >
                  <td className="py-3 px-2">
                    <input type="checkbox" className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2" />
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{user.email}</td>
                  <td className="py-3 px-2 text-sm">
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                      user.role === 'ADMIN' ? 'bg-app-primary/20 text-app-primary' : 'bg-app-gray/20 text-app-gray'
                    }`}>
                      {user.role}
                    </span>
                  </td>
                  <td className="py-3 px-2 text-sm">
                    <button
                      onClick={() => toggleUserEnabled(user.id, user.enabled)}
                      className={`px-2 py-1 rounded-full text-xs font-medium ${
                        user.enabled ? 'bg-app-success/20 text-app-success' : 'bg-app-danger/20 text-app-danger'
                      }`}
                    >
                      {user.enabled ? '' : ''}
                    </button>
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{user.credits_remaining?.toLocaleString()}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {new Date(user.expiry).toLocaleDateString()}
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {new Date(user.created_at).toLocaleDateString()}
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {user.last_login_at ? new Date(user.last_login_at).toLocaleDateString() : 'Never'}
                  </td>
                  <td className="py-3 px-2">
                    <div className="flex items-center space-x-1">
                      <button className="p-1 text-app-text-secondary hover:text-app-primary rounded hover:bg-app-card-deep">
                        <SafeIcon icon={FiEye} className="w-4 h-4" />
                      </button>
                      <button 
                        onClick={() => setEditingUser(user)}
                        className="p-1 text-app-text-secondary hover:text-app-primary rounded hover:bg-app-card-deep"
                      >
                        <SafeIcon icon={FiEdit} className="w-4 h-4" />
                      </button>
                      <button className="p-1 text-app-text-secondary hover:text-app-primary rounded hover:bg-app-card-deep">
                        <SafeIcon icon={FiKey} className="w-4 h-4" />
                      </button>
                      <button 
                        onClick={() => handleDeleteUser(user.id)}
                        className="p-1 text-app-text-secondary hover:text-app-danger rounded hover:bg-app-card-deep"
                      >
                        <SafeIcon icon={FiTrash2} className="w-4 h-4" />
                      </button>
                    </div>
                  </td>
                </motion.tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Users;