import React, { useState } from 'react';
import { motion } from 'framer-motion';

const ChangeVoiceSettingCard = () => {
  const [enabled, setEnabled] = useState(false);
  const [settings, setSettings] = useState({
    speed: 1.0,
    style: 0,
    stability: 50,
    similarity: 75,
    speakerBoost: false
  });

  const handleSliderChange = (key, value) => {
    setSettings(prev => ({ ...prev, [key]: value }));
  };

  return (
    <motion.div 
      className="bg-app-card rounded-card p-6 shadow-card hover:shadow-card-hover transition-shadow"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.2 }}
    >
      <div className="flex items-center justify-between mb-4">
        <h3 className="text-lg font-semibold text-app-text-primary">Change voice setting</h3>
        <label className="relative inline-flex items-center cursor-pointer">
          <input
            type="checkbox"
            checked={enabled}
            onChange={(e) => setEnabled(e.target.checked)}
            className="sr-only peer"
          />
          <div className="w-11 h-6 bg-gray-600 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-app-primary/20 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-app-primary"></div>
        </label>
      </div>
      
      <div className={`space-y-4 ${!enabled ? 'opacity-50 pointer-events-none' : ''}`}>
        <div>
          <div className="flex justify-between items-center mb-2">
            <label className="text-sm text-app-text-primary">Speed</label>
            <span className="text-sm text-app-text-secondary">{settings.speed.toFixed(2)}</span>
          </div>
          <input
            type="range"
            min="0.25"
            max="4.0"
            step="0.05"
            value={settings.speed}
            onChange={(e) => handleSliderChange('speed', parseFloat(e.target.value))}
            className="w-full h-2 bg-gray-600 rounded-lg appearance-none cursor-pointer slider"
          />
        </div>

        <div>
          <div className="flex justify-between items-center mb-2">
            <label className="text-sm text-app-text-primary">Style</label>
            <span className="text-sm text-app-text-secondary">{settings.style}%</span>
          </div>
          <input
            type="range"
            min="0"
            max="100"
            value={settings.style}
            onChange={(e) => handleSliderChange('style', parseInt(e.target.value))}
            className="w-full h-2 bg-gray-600 rounded-lg appearance-none cursor-pointer slider"
          />
        </div>

        <div>
          <div className="flex justify-between items-center mb-2">
            <label className="text-sm text-app-text-primary">Stability</label>
            <span className="text-sm text-app-text-secondary">{settings.stability}%</span>
          </div>
          <input
            type="range"
            min="0"
            max="100"
            value={settings.stability}
            onChange={(e) => handleSliderChange('stability', parseInt(e.target.value))}
            className="w-full h-2 bg-gray-600 rounded-lg appearance-none cursor-pointer slider"
          />
        </div>

        <div>
          <div className="flex justify-between items-center mb-2">
            <label className="text-sm text-app-text-primary">Similarity</label>
            <span className="text-sm text-app-text-secondary">{settings.similarity}%</span>
          </div>
          <input
            type="range"
            min="0"
            max="100"
            value={settings.similarity}
            onChange={(e) => handleSliderChange('similarity', parseInt(e.target.value))}
            className="w-full h-2 bg-gray-600 rounded-lg appearance-none cursor-pointer slider"
          />
        </div>

        <div className="flex items-center justify-between">
          <label className="text-sm text-app-text-primary">Speaker Boost</label>
          <input
            type="checkbox"
            checked={settings.speakerBoost}
            onChange={(e) => handleSliderChange('speakerBoost', e.target.checked)}
            className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2"
          />
        </div>
      </div>
    </motion.div>
  );
};

export default ChangeVoiceSettingCard;