import React, { useState } from 'react';
import { Navigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useAuth } from '../../contexts/AuthContext';
import SafeIcon from '../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiMic, FiEye, FiEyeOff, FiLoader, FiInfo } = FiIcons;

const LoginPage = ({ isAdmin }) => {
  const { user, login, loading } = useAuth();
  const [formData, setFormData] = useState({ 
    email: isAdmin ? 'admin@voicebaba.ai' : 'demo@voicebaba.ai', 
    password: isAdmin ? 'admin123' : 'demo123' 
  });
  const [showPassword, setShowPassword] = useState(false);
  const [isLogging, setIsLogging] = useState(false);
  const [error, setError] = useState('');

  if (loading) {
    return (
      <div className="min-h-screen bg-app-bg flex items-center justify-center">
        <SafeIcon icon={FiLoader} className="w-8 h-8 text-app-primary animate-spin" />
      </div>
    );
  }

  if (user) {
    return <Navigate to="/" replace />;
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLogging(true);
    setError('');

    const result = await login(formData.email, formData.password, isAdmin);
    
    if (!result.success) {
      setError(result.error || 'Login failed');
    }
    
    setIsLogging(false);
  };

  const demoAccounts = isAdmin ? [
    { email: 'admin@voicebaba.ai', password: 'admin123', role: 'Admin' }
  ] : [
    { email: 'demo@voicebaba.ai', password: 'demo123', role: 'User' },
    { email: 'user@example.com', password: 'password', role: 'User' }
  ];

  return (
    <div className="min-h-screen bg-app-bg flex items-center justify-center p-4">
      <motion.div 
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="w-full max-w-md"
      >
        <div className="bg-app-card rounded-card p-8 shadow-card">
          <div className="text-center mb-8">
            <div className="flex items-center justify-center mb-4">
              <SafeIcon icon={FiMic} className="w-10 h-10 text-app-primary mr-3" />
              <div>
                <h1 className="text-2xl font-bold text-app-text-primary">Voice Baba AI</h1>
                <p className="text-sm text-app-text-secondary">
                  {isAdmin ? 'Admin Dashboard' : 'Professional Voice Creation Tool'}
                </p>
              </div>
            </div>
          </div>

          {/* Demo Accounts Info */}
          <div className="mb-6 p-4 bg-app-info/10 border border-app-info/20 rounded-lg">
            <div className="flex items-start">
              <SafeIcon icon={FiInfo} className="w-5 h-5 text-app-info mr-3 mt-0.5" />
              <div className="text-sm text-app-info">
                <p className="font-medium mb-2">Demo Accounts:</p>
                {demoAccounts.map((account, index) => (
                  <div key={index} className="mb-2 last:mb-0">
                    <div className="font-mono text-xs bg-app-info/10 p-2 rounded">
                      <div><strong>Email:</strong> {account.email}</div>
                      <div><strong>Password:</strong> {account.password}</div>
                      <div><strong>Role:</strong> {account.role}</div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-app-text-primary mb-2">
                Email
              </label>
              <input
                type="email"
                value={formData.email}
                onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
                className="w-full px-4 py-3 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
                placeholder="Enter your email"
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-app-text-primary mb-2">
                Password
              </label>
              <div className="relative">
                <input
                  type={showPassword ? 'text' : 'password'}
                  value={formData.password}
                  onChange={(e) => setFormData(prev => ({ ...prev, password: e.target.value }))}
                  className="w-full px-4 py-3 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent pr-12"
                  placeholder="Enter your password"
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-app-text-secondary hover:text-app-text-primary"
                >
                  <SafeIcon icon={showPassword ? FiEyeOff : FiEye} className="w-5 h-5" />
                </button>
              </div>
            </div>

            {error && (
              <div className="p-3 bg-app-danger/10 border border-app-danger/20 rounded-lg text-app-danger text-sm">
                {error}
              </div>
            )}

            <button
              type="submit"
              disabled={isLogging}
              className="w-full bg-gradient-to-r from-app-primary to-app-primary-end text-white py-3 px-4 rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
            >
              {isLogging ? (
                <SafeIcon icon={FiLoader} className="w-5 h-5 animate-spin" />
              ) : (
                'Sign In'
              )}
            </button>
          </form>

          {/* Quick Access Buttons */}
          <div className="mt-6 space-y-2">
            {demoAccounts.map((account, index) => (
              <button
                key={index}
                onClick={() => setFormData({ email: account.email, password: account.password })}
                className="w-full text-left px-4 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-secondary hover:text-app-text-primary hover:bg-gray-600 transition-colors text-sm"
              >
                Quick Login: {account.role} ({account.email})
              </button>
            ))}
          </div>
        </div>
      </motion.div>
    </div>
  );
};

export default LoginPage;