import React, { useState, useEffect } from 'react';
import SafeIcon from '../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiCheck } = FiIcons;

const FooterStatusBar = () => {
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const formatTime = (date) => {
    return date.toLocaleTimeString('en-US', { 
      hour12: false,
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  };

  return (
    <footer className="bg-app-card border-t border-gray-700 px-6 py-3">
      <div className="container mx-auto flex items-center justify-between">
        <div className="flex items-center text-app-success">
          <SafeIcon icon={FiCheck} className="w-4 h-4 mr-2" />
          <span className="text-sm font-medium">Ready</span>
        </div>
        
        <div className="text-sm text-app-text-secondary font-mono">
          {formatTime(currentTime)}
        </div>
      </div>
    </footer>
  );
};

export default FooterStatusBar;