import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useAdmin } from '../../../contexts/AdminContext';
import SafeIcon from '../../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiSave, FiRefreshCw, FiInfo } = FiIcons;

const Billing = () => {
  const { globalPool, setGlobalPool } = useAdmin();
  const [formData, setFormData] = useState({
    total_pool: globalPool.total_pool,
    period: globalPool.period,
    reset_on: globalPool.reset_on.toISOString().split('T')[0],
    carryover: globalPool.carryover
  });

  const [history] = useState([
    {
      id: 1,
      date: new Date(),
      action: 'Pool Updated',
      before: '800,000',
      after: '1,000,000',
      actor: 'admin@example.com',
      note: 'Increased pool for Q4'
    }
  ]);

  const handleSave = () => {
    const updatedPool = {
      ...globalPool,
      total_pool: parseInt(formData.total_pool),
      period: formData.period,
      reset_on: new Date(formData.reset_on),
      carryover: formData.carryover
    };
    
    setGlobalPool(updatedPool);
    toast.success('Global pool settings updated');
  };

  const handleRecalculate = () => {
    // Simulate recalculation
    const allocated = Math.floor(Math.random() * formData.total_pool * 0.3);
    const remaining = formData.total_pool - allocated;
    
    setGlobalPool(prev =>({ 
      ...prev, 
      allocated, 
      remaining,
      total_pool: parseInt(formData.total_pool)
    }));
    
    toast.success('Pool recalculated');
  };

  return (
    <div className="space-y-6">
      {/* Configuration Form */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-6">Global Pool Configuration</h3>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium text-app-text-primary mb-2">
              Total Pool
            </label>
            <input
              type="number"
              value={formData.total_pool}
              onChange={(e) => setFormData(prev => ({ ...prev, total_pool: e.target.value }))}
              className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-app-text-primary mb-2">
              Period
            </label>
            <select
              value={formData.period}
              onChange={(e) => setFormData(prev => ({ ...prev, period: e.target.value }))}
              className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
            >
              <option value="yearly">Yearly</option>
              <option value="monthly">Monthly</option>
              <option value="custom">Custom</option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-app-text-primary mb-2">
              Reset On
            </label>
            <input
              type="date"
              value={formData.reset_on}
              onChange={(e) => setFormData(prev => ({ ...prev, reset_on: e.target.value }))}
              className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
            />
          </div>

          <div className="flex items-center">
            <label className="flex items-center cursor-pointer">
              <input
                type="checkbox"
                checked={formData.carryover}
                onChange={(e) => setFormData(prev => ({ ...prev, carryover: e.target.checked }))}
                className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2 mr-3"
              />
              <span className="text-sm text-app-text-primary">Carryover remaining credits</span>
            </label>
          </div>
        </div>

        <div className="flex space-x-4 mt-6">
          <button
            onClick={handleSave}
            className="flex items-center px-6 py-2 bg-gradient-to-r from-app-primary to-app-primary-end text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
          >
            <SafeIcon icon={FiSave} className="w-4 h-4 mr-2" />
            Save
          </button>
          <button
            onClick={handleRecalculate}
            className="flex items-center px-6 py-2 bg-app-card-deep border border-gray-600 text-app-text-primary rounded-full font-semibold hover:bg-gray-600 transition-colors"
          >
            <SafeIcon icon={FiRefreshCw} className="w-4 h-4 mr-2" />
            Recalculate
          </button>
        </div>

        <div className="mt-6 p-4 bg-app-info/10 border border-app-info/20 rounded-lg flex items-start">
          <SafeIcon icon={FiInfo} className="w-5 h-5 text-app-info mr-3 mt-0.5" />
          <div className="text-sm text-app-info">
            <p className="font-medium">Changes affect quota immediately.</p>
            <p className="mt-1">Current allocation: {globalPool.allocated.toLocaleString()} credits</p>
            <p>Remaining in pool: {globalPool.remaining.toLocaleString()} credits</p>
          </div>
        </div>
      </motion.div>

      {/* History */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-6">History</h3>
        
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Date</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Action</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Before</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">After</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Actor</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Note</th>
              </tr>
            </thead>
            <tbody>
              {history.map((record) => (
                <tr key={record.id} className="border-b border-gray-700 hover:bg-app-card-deep">
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {record.date.toLocaleDateString()}
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{record.action}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{record.before}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{record.after}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{record.actor}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{record.note}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </motion.div>
    </div>
  );
};

export default Billing;