import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { 
  FiHome, FiUsers, FiCreditCard, FiDollarSign, FiSettings, 
  FiBarChart3, FiShield, FiDatabase, FiChevronLeft, FiMic 
} = FiIcons;

const Sidebar = ({ collapsed, onToggle }) => {
  const location = useLocation();

  const menuItems = [
    { path: '/', icon: FiHome, label: 'Overview' },
    { path: '/users', icon: FiUsers, label: 'Users' },
    { path: '/licences', icon: FiCreditCard, label: 'Licences' },
    { path: '/billing', icon: FiDollarSign, label: 'Billing / Global Pool' },
    { path: '/api-config', icon: FiDatabase, label: 'API Configuration' },
    { path: '/quota-logs', icon: FiBarChart3, label: 'Quota & Logs' },
    { path: '/audit-alerts', icon: FiShield, label: 'Audit & Alerts' },
    { path: '/settings', icon: FiSettings, label: 'Settings' }
  ];

  return (
    <motion.div 
      className={`fixed left-0 top-0 h-full bg-app-card border-r border-gray-700 transition-all duration-300 z-30 ${
        collapsed ? 'w-16' : 'w-64'
      }`}
      initial={false}
      animate={{ width: collapsed ? 64 : 256 }}
    >
      <div className="p-4">
        <div className="flex items-center justify-between">
          {!collapsed && (
            <div className="flex items-center">
              <SafeIcon icon={FiMic} className="w-8 h-8 text-app-primary mr-3" />
              <div>
                <h1 className="text-lg font-bold text-app-text-primary">Voice Baba AI</h1>
                <p className="text-xs text-app-text-secondary">Admin Dashboard</p>
              </div>
            </div>
          )}
          <button
            onClick={onToggle}
            className="p-2 text-app-text-secondary hover:text-app-text-primary rounded-lg hover:bg-app-card-deep"
          >
            <SafeIcon 
              icon={FiChevronLeft} 
              className={`w-5 h-5 transition-transform ${collapsed ? 'rotate-180' : ''}`} 
            />
          </button>
        </div>
      </div>

      <nav className="mt-8">
        {menuItems.map((item) => {
          const isActive = location.pathname === item.path;
          return (
            <Link
              key={item.path}
              to={item.path}
              className={`flex items-center px-4 py-3 text-sm transition-colors relative ${
                isActive 
                  ? 'text-app-primary bg-app-primary/10 border-r-2 border-app-primary' 
                  : 'text-app-text-secondary hover:text-app-text-primary hover:bg-app-card-deep'
              }`}
            >
              <SafeIcon icon={item.icon} className="w-5 h-5 min-w-[20px]" />
              {!collapsed && (
                <span className="ml-3 truncate">{item.label}</span>
              )}
            </Link>
          );
        })}
      </nav>
    </motion.div>
  );
};

export default Sidebar;