import React, { useState, useRef, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useVoice } from '../../contexts/VoiceContext';
import { useAuth } from '../../contexts/AuthContext';
import SafeIcon from '../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { 
  FiPlay, FiSquare, FiUpload, FiFolderPlus, FiFolder, 
  FiFileText, FiMusic, FiTrash2, FiSave, FiMoreVertical 
} = FiIcons;

const SubtitlesPanel = () => {
  const { user } = useAuth();
  const {
    subtitles,
    isProcessing,
    processedCount,
    processingCount,
    elapsedTime,
    addSubtitles,
    clearSubtitles,
    startProcessing,
    stopProcessing,
    setSubtitles,
    setProcessedCount,
    setElapsedTime
  } = useVoice();

  const [selectedRows, setSelectedRows] = useState(new Set());
  const fileInputRef = useRef(null);
  const folderInputRef = useRef(null);
  const intervalRef = useRef(null);

  const canProcess = user?.licence?.credits_remaining > 0 && 
                    new Date() < new Date(user?.licence?.expiry);

  useEffect(() => {
    if (isProcessing) {
      intervalRef.current = setInterval(() => {
        setElapsedTime(prev => prev + 1);
      }, 1000);
    } else {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isProcessing, setElapsedTime]);

  const handleStart = () => {
    if (!canProcess) {
      toast.error('Cannot process: insufficient credits or expired licence');
      return;
    }

    if (subtitles.length === 0) {
      toast.error('No subtitles to process');
      return;
    }

    startProcessing();
    
    // Simulate processing
    const totalItems = subtitles.length;
    let processed = 0;

    const processNext = () => {
      if (processed < totalItems) {
        setSubtitles(prev => prev.map((item, index) => 
          index === processed ? { ...item, status: 'Generating audio' } : item
        ));
        
        setTimeout(() => {
          setSubtitles(prev => prev.map((item, index) => 
            index === processed ? { ...item, status: 'Done' } : item
          ));
          processed++;
          setProcessedCount(processed);
          
          if (processed < totalItems) {
            processNext();
          } else {
            stopProcessing();
            toast.success('Processing completed! Processed files.', {
              duration: 0,
              id: 'processing-complete'
            });
            
            // Show save dialog after user clicks OK
            setTimeout(() => {
              const saveFile = window.confirm('Processing completed! Save output file?');
              if (saveFile) {
                // Simulate save dialog
                const filename = prompt('Save as:', 'Output.mp3');
                if (filename) {
                  toast.success(`File saved as ${filename}`);
                }
              }
            }, 1000);
          }
        }, 2000);
      }
    };

    processNext();
  };

  const handleStop = () => {
    stopProcessing();
    toast.success('Processing stopped');
  };

  const handleImportFile = () => {
    fileInputRef.current?.click();
  };

  const handleImportFolder = () => {
    folderInputRef.current?.click();
  };

  const handleFileChange = (event) => {
    const files = Array.from(event.target.files);
    const validFiles = files.filter(file => 
      file.name.endsWith('.srt') || file.name.endsWith('.txt') || file.name.endsWith('.dgt')
    );

    if (validFiles.length > 0) {
      const newSubtitles = validFiles.map((file, index) => ({
        id: Date.now() + index,
        output: file.name.replace(/\.[^/.]+$/, '.mp3'),
        timing: '00:00:00',
        content: `Content from ${file.name}`,
        voice_id: 'default',
        status: 'Ready'
      }));

      addSubtitles(newSubtitles);
      toast.success(`Loaded ${validFiles.length} subtitles`);
    }
  };

  const handleRowContextMenu = (e, id) => {
    e.preventDefault();
    const confirmed = window.confirm('Delete this item?');
    if (confirmed) {
      setSubtitles(prev => prev.filter(item => item.id !== id));
      toast.success('Item deleted');
    }
  };

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const totalCount = subtitles.length;

  return (
    <motion.div 
      className="bg-app-card rounded-card p-6 shadow-card hover:shadow-card-hover transition-shadow"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.5 }}
    >
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-4">
          <h3 className="text-lg font-semibold text-app-text-primary">
            Subtitles (Done: {processedCount} Processing: {processingCount} Total: {totalCount}) 
            Elapsed: {formatTime(elapsedTime)}
          </h3>
        </div>
      </div>

      <div className="flex flex-wrap gap-3 mb-6">
        <button
          onClick={handleStart}
          disabled={isProcessing || !canProcess || subtitles.length === 0}
          className="flex items-center px-4 py-2 bg-app-success text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          <SafeIcon icon={FiPlay} className="w-4 h-4 mr-2" />
          Start
        </button>

        <button
          onClick={handleStop}
          disabled={!isProcessing}
          className="flex items-center px-4 py-2 bg-app-danger text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          <SafeIcon icon={FiSquare} className="w-4 h-4 mr-2" />
          Stop
        </button>

        <button
          onClick={handleImportFile}
          className="flex items-center px-4 py-2 bg-app-blue text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiUpload} className="w-4 h-4 mr-2" />
          Import File
        </button>

        <button
          onClick={handleImportFolder}
          className="flex items-center px-4 py-2 bg-app-violet text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiFolderPlus} className="w-4 h-4 mr-2" />
          Import Folder
        </button>

        <button
          className="flex items-center px-4 py-2 bg-app-info text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiFolder} className="w-4 h-4 mr-2" />
          Open Audio Output
        </button>

        <button
          className="flex items-center px-4 py-2 bg-app-teal text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiFileText} className="w-4 h-4 mr-2" />
          Create SRT
        </button>

        <button
          className="flex items-center px-4 py-2 bg-app-pink text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiMusic} className="w-4 h-4 mr-2" />
          Join Mp3
        </button>

        <button
          onClick={clearSubtitles}
          className="flex items-center px-4 py-2 bg-app-gray text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiTrash2} className="w-4 h-4 mr-2" />
          Clear All
        </button>

        <button
          className="flex items-center px-4 py-2 bg-app-amber text-white rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
        >
          <SafeIcon icon={FiSave} className="w-4 h-4 mr-2" />
          Backup
        </button>
      </div>

      <div className="overflow-auto max-h-96">
        {subtitles.length === 0 ? (
          <div className="text-center py-12 text-app-text-secondary">
            <SafeIcon icon={FiUpload} className="w-12 h-12 mx-auto mb-4 opacity-50" />
            <p>No processes yet  Import files to get started</p>
          </div>
        ) : (
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">ID</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Output</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Timing</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Content</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Voice ID</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Status</th>
              </tr>
            </thead>
            <tbody>
              {subtitles.map((item) => (
                <tr 
                  key={item.id}
                  onContextMenu={(e) => handleRowContextMenu(e, item.id)}
                  className="border-b border-gray-700 hover:bg-app-card-deep cursor-context-menu"
                >
                  <td className="py-3 px-2 text-sm text-app-text-primary">{item.id}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{item.output}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{item.timing}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary max-w-xs truncate">{item.content}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{item.voice_id}</td>
                  <td className="py-3 px-2 text-sm">
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                      item.status === 'Done' ? 'bg-app-success/20 text-app-success' :
                      item.status === 'Generating audio' ? 'bg-app-amber/20 text-app-amber' :
                      item.status === 'Failed' ? 'bg-app-danger/20 text-app-danger' :
                      'bg-app-gray/20 text-app-gray'
                    }`}>
                      {item.status}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

      <input
        ref={fileInputRef}
        type="file"
        accept=".srt,.txt,.dgt"
        multiple
        onChange={handleFileChange}
        className="hidden"
      />

      <input
        ref={folderInputRef}
        type="file"
        webkitdirectory=""
        multiple
        onChange={handleFileChange}
        className="hidden"
      />
    </motion.div>
  );
};

export default SubtitlesPanel;