import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import SafeIcon from '../../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiX, FiPlus, FiTrash2 } = FiIcons;

const AdvancedSettingsModal = ({ isOpen, onClose }) => {
  const [settings, setSettings] = useState({
    delayBetweenSegments: 1.0,
    silentByCharacter: true,
    silentRules: [
      { character: ';', duration: 0.3 },
      { character: ':!', duration: 0.5 }
    ],
    autoRemoveSpecialChars: false,
    maxSegmentLength: 10000
  });

  const handleSave = () => {
    toast.success('Advanced settings saved');
    onClose();
  };

  const addSilentRule = () => {
    setSettings(prev => ({
      ...prev,
      silentRules: [...prev.silentRules, { character: '', duration: 0.5 }]
    }));
  };

  const removeSilentRule = (index) => {
    setSettings(prev => ({
      ...prev,
      silentRules: prev.silentRules.filter((_, i) => i !== index)
    }));
  };

  const updateSilentRule = (index, field, value) => {
    setSettings(prev => ({
      ...prev,
      silentRules: prev.silentRules.map((rule, i) => 
        i === index ? { ...rule, [field]: value } : rule
      )
    }));
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-50">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            className="bg-app-card rounded-card p-6 w-full max-w-md"
          >
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-bold text-app-text-primary">Advanced Settings</h2>
              <button
                onClick={onClose}
                className="p-2 text-app-text-secondary hover:text-app-text-primary"
              >
                <SafeIcon icon={FiX} className="w-6 h-6" />
              </button>
            </div>

            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-app-text-primary mb-2">
                  Delay between segments (seconds)
                </label>
                <input
                  type="number"
                  step="0.1"
                  min="0"
                  value={settings.delayBetweenSegments}
                  onChange={(e) => setSettings(prev => ({ 
                    ...prev, 
                    delayBetweenSegments: parseFloat(e.target.value) || 0 
                  }))}
                  className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
                />
              </div>

              <div>
                <div className="flex items-center justify-between mb-3">
                  <label className="text-sm font-medium text-app-text-primary">
                    Silent by character
                  </label>
                  <input
                    type="checkbox"
                    checked={settings.silentByCharacter}
                    onChange={(e) => setSettings(prev => ({ 
                      ...prev, 
                      silentByCharacter: e.target.checked 
                    }))}
                    className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2"
                  />
                </div>
                
                {settings.silentByCharacter && (
                  <div className="space-y-2">
                    {settings.silentRules.map((rule, index) => (
                      <div key={index} className="flex items-center space-x-2">
                        <input
                          type="text"
                          placeholder="Character(s)"
                          value={rule.character}
                          onChange={(e) => updateSilentRule(index, 'character', e.target.value)}
                          className="flex-1 px-2 py-1 bg-app-card-deep border border-gray-600 rounded text-app-text-primary text-sm focus:outline-none focus:ring-1 focus:ring-app-primary"
                        />
                        <span className="text-app-text-secondary text-sm"></span>
                        <input
                          type="number"
                          step="0.1"
                          min="0"
                          value={rule.duration}
                          onChange={(e) => updateSilentRule(index, 'duration', parseFloat(e.target.value) || 0)}
                          className="w-16 px-2 py-1 bg-app-card-deep border border-gray-600 rounded text-app-text-primary text-sm focus:outline-none focus:ring-1 focus:ring-app-primary"
                        />
                        <span className="text-app-text-secondary text-sm">s</span>
                        <button
                          onClick={() => removeSilentRule(index)}
                          className="p-1 text-app-danger hover:bg-app-danger/20 rounded"
                        >
                          <SafeIcon icon={FiTrash2} className="w-4 h-4" />
                        </button>
                      </div>
                    ))}
                    <button
                      onClick={addSilentRule}
                      className="flex items-center text-app-primary hover:text-app-primary-end text-sm"
                    >
                      <SafeIcon icon={FiPlus} className="w-4 h-4 mr-1" />
                      Add Rule
                    </button>
                  </div>
                )}
              </div>

              <div className="flex items-center justify-between">
                <label className="text-sm font-medium text-app-text-primary">
                  Auto remove special characters
                </label>
                <input
                  type="checkbox"
                  checked={settings.autoRemoveSpecialChars}
                  onChange={(e) => setSettings(prev => ({ 
                    ...prev, 
                    autoRemoveSpecialChars: e.target.checked 
                  }))}
                  className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-app-text-primary mb-2">
                  Maximum segment length (characters)
                </label>
                <input
                  type="number"
                  min="100"
                  value={settings.maxSegmentLength}
                  onChange={(e) => setSettings(prev => ({ 
                    ...prev, 
                    maxSegmentLength: parseInt(e.target.value) || 1000 
                  }))}
                  className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
                />
              </div>
            </div>

            <div className="flex justify-end space-x-3 mt-8">
              <button
                onClick={onClose}
                className="px-4 py-2 bg-app-card-deep border border-gray-600 text-app-text-primary rounded-full font-semibold hover:bg-gray-600 transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-gradient-to-r from-app-primary to-app-primary-end text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
              >
                OK
              </button>
            </div>
          </motion.div>
        </div>
      )}
    </AnimatePresence>
  );
};

export default AdvancedSettingsModal;