import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useAdmin } from '../../../contexts/AdminContext';
import SafeIcon from '../../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiEye, FiEyeOff, FiSave, FiTrash2, FiCheckCircle, FiXCircle, FiRefreshCw, FiAlertTriangle } = FiIcons;

const ApiConfiguration = () => {
  const { apiConfig, setApiConfig } = useAdmin();
  const [showApiKey, setShowApiKey] = useState(false);
  const [formData, setFormData] = useState({
    api_key: apiConfig.api_key,
    timeout_sec: apiConfig.timeout_sec
  });
  const [testing, setTesting] = useState(false);

  const handleSave = () => {
    setApiConfig({
      ...apiConfig,
      api_key: formData.api_key,
      timeout_sec: formData.timeout_sec,
      configured: formData.api_key.length > 0
    });
    toast.success('API configuration saved');
  };

  const handleDelete = () => {
    const confirmed = window.confirm('Are you sure you want to delete the API key?');
    if (confirmed) {
      setFormData({ ...formData, api_key: '' });
      setApiConfig({ ...apiConfig, api_key: '', configured: false });
      toast.success('API key deleted');
    }
  };

  const handleTestConnection = async () => {
    if (!formData.api_key) {
      toast.error('Please enter an API key first');
      return;
    }

    setTesting(true);
    try {
      // Simulate API test
      await new Promise(resolve => setTimeout(resolve, 2000));
      toast.success('Connection test successful');
    } catch (error) {
      toast.error('Connection test failed');
    } finally {
      setTesting(false);
    }
  };

  const getStatusChip = () => {
    if (!apiConfig.configured) {
      return (
        <span className="flex items-center px-3 py-1 bg-app-danger/20 text-app-danger rounded-full text-sm font-medium">
          <SafeIcon icon={FiXCircle} className="w-4 h-4 mr-1" />
          Not Configured
        </span>
      );
    }
    return (
      <span className="flex items-center px-3 py-1 bg-app-success/20 text-app-success rounded-full text-sm font-medium">
        <SafeIcon icon={FiCheckCircle} className="w-4 h-4 mr-1" />
        Configured
      </span>
    );
  };

  return (
    <div className="space-y-6">
      {/* API Key Management */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
      >
        <div className="flex items-center justify-between mb-6">
          <h3 className="text-lg font-semibold text-app-text-primary">API Key Management</h3>
          {getStatusChip()}
        </div>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-app-text-primary mb-2">
              ElevenLabs API Key
            </label>
            <div className="relative">
              <input
                type={showApiKey ? 'text' : 'password'}
                value={formData.api_key}
                onChange={(e) => setFormData(prev => ({ ...prev, api_key: e.target.value }))}
                placeholder="Enter your ElevenLabs API key"
                className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent pr-20"
              />
              <button
                type="button"
                onClick={() => setShowApiKey(!showApiKey)}
                className="absolute right-12 top-1/2 transform -translate-y-1/2 p-1 text-app-text-secondary hover:text-app-text-primary"
              >
                <SafeIcon icon={showApiKey ? FiEyeOff : FiEye} className="w-4 h-4" />
              </button>
              <button
                type="button"
                onClick={handleDelete}
                className="absolute right-3 top-1/2 transform -translate-y-1/2 p-1 text-app-danger hover:text-app-danger/80"
              >
                <SafeIcon icon={FiTrash2} className="w-4 h-4" />
              </button>
            </div>
          </div>

          <div className="flex space-x-4">
            <button
              onClick={handleSave}
              className="flex items-center px-6 py-2 bg-gradient-to-r from-app-primary to-app-primary-end text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
            >
              <SafeIcon icon={FiSave} className="w-4 h-4 mr-2" />
              Save
            </button>
            <button
              onClick={handleTestConnection}
              disabled={testing || !formData.api_key}
              className="flex items-center px-6 py-2 bg-app-card-deep border border-gray-600 text-app-text-primary rounded-full font-semibold hover:bg-gray-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <SafeIcon icon={testing ? FiRefreshCw : FiCheckCircle} className={`w-4 h-4 mr-2 ${testing ? 'animate-spin' : ''}`} />
              Test Connection
            </button>
          </div>
        </div>
      </motion.div>

      {/* Advanced Settings */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-6">Advanced Settings</h3>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-app-text-primary mb-2">
              Request Timeout (seconds)
            </label>
            <input
              type="number"
              min="10"
              max="300"
              value={formData.timeout_sec}
              onChange={(e) => setFormData(prev => ({ ...prev, timeout_sec: parseInt(e.target.value) || 60 }))}
              className="w-full max-w-xs px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
            />
            <p className="text-xs text-app-text-secondary mt-1">Default: 60 seconds</p>
          </div>

          <button
            onClick={handleSave}
            className="flex items-center px-6 py-2 bg-gradient-to-r from-app-primary to-app-primary-end text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
          >
            <SafeIcon icon={FiSave} className="w-4 h-4 mr-2" />
            Save All
          </button>
        </div>
      </motion.div>

      {/* Danger Zone */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card border border-app-danger/20"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.2 }}
      >
        <h3 className="text-lg font-semibold text-app-danger mb-6 flex items-center">
          <SafeIcon icon={FiAlertTriangle} className="w-5 h-5 mr-2" />
          Danger Zone
        </h3>
        
        <div className="space-y-4">
          <div>
            <h4 className="text-sm font-medium text-app-text-primary mb-2">Rotate API Key</h4>
            <p className="text-sm text-app-text-secondary mb-4">
              Generate a new API key and update your configuration. Ensure you have the new key ready before rotating.
            </p>
            <button className="flex items-center px-4 py-2 bg-app-danger text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200">
              <SafeIcon icon={FiRefreshCw} className="w-4 h-4 mr-2" />
              Rotate Key
            </button>
          </div>
        </div>
      </motion.div>
    </div>
  );
};

export default ApiConfiguration;