import React from 'react';
import { motion } from 'framer-motion';
import KPICard from '../Components/KPICard';
import ChartCard from '../Components/ChartCard';
import DataTable from '../Components/DataTable';
import { useAdmin } from '../../../contexts/AdminContext';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiUsers, FiCreditCard, FiPhone, FiAlertTriangle, FiKey } = FiIcons;

const Overview = () => {
  const { users, globalPool, apiConfig } = useAdmin();

  const kpiData = [
    {
      title: 'Total Users',
      value: users.length,
      icon: FiUsers,
      color: 'text-app-blue'
    },
    {
      title: 'Active Users (30d)',
      value: users.filter(u => u.last_login_at && 
        new Date(u.last_login_at) > new Date(Date.now() - 30 * 24 * 60 * 60 * 1000)
      ).length,
      icon: FiUsers,
      color: 'text-app-success'
    },
    {
      title: 'Allocated Credits',
      value: globalPool.allocated.toLocaleString(),
      icon: FiCreditCard,
      color: 'text-app-primary'
    },
    {
      title: 'Pool Remaining',
      value: globalPool.remaining.toLocaleString(),
      icon: FiCreditCard,
      color: 'text-app-teal'
    },
    {
      title: 'Today\'s TTS Calls',
      value: '1,247',
      icon: FiPhone,
      color: 'text-app-violet'
    },
    {
      title: '24h Error Rate',
      value: '2.1%',
      icon: FiAlertTriangle,
      color: 'text-app-amber'
    },
    {
      title: 'Licences Expiring  7d',
      value: users.filter(u => {
        const expiry = new Date(u.expiry);
        const sevenDaysFromNow = new Date(Date.now() + 7 * 24 * 60 * 60 * 1000);
        return expiry <= sevenDaysFromNow;
      }).length,
      icon: FiAlertTriangle,
      color: 'text-app-danger'
    },
    {
      title: 'API Key Status',
      value: apiConfig.configured ? 'Configured' : 'Not Configured',
      icon: FiKey,
      color: apiConfig.configured ? 'text-app-success' : 'text-app-danger'
    }
  ];

  const recentUsersColumns = [
    { key: 'email', label: 'Email' },
    { key: 'role', label: 'Role' },
    { key: 'enabled', label: 'Enabled', render: (value) => value ? '' : '' },
    { key: 'credits_remaining', label: 'Credits', render: (value) => value?.toLocaleString() },
    { key: 'expiry', label: 'Expiry', render: (value) => new Date(value).toLocaleDateString() },
    { key: 'last_login_at', label: 'Last Login', render: (value) => value ? new Date(value).toLocaleDateString() : 'Never' }
  ];

  const recentJobsColumns = [
    { key: 'user', label: 'User' },
    { key: 'title', label: 'Title' },
    { key: 'status', label: 'Status' },
    { key: 'duration', label: 'Duration' },
    { key: 'created_at', label: 'Created' },
    { key: 'error', label: 'Error?' }
  ];

  const mockJobs = [
    { id: 1, user: 'user@example.com', title: 'Sample TTS Job', status: 'completed', duration: '2.3s', created_at: new Date(), error: false }
  ];

  const errorsColumns = [
    { key: 'time', label: 'Time' },
    { key: 'endpoint', label: 'Endpoint' },
    { key: 'code', label: 'Code' },
    { key: 'message', label: 'Message' },
    { key: 'user', label: 'User' },
    { key: 'correlation_id', label: 'Correlation ID' }
  ];

  const mockErrors = [];

  return (
    <div className="space-y-6">
      {/* KPI Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {kpiData.map((kpi, index) => (
          <motion.div
            key={kpi.title}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
          >
            <KPICard {...kpi} />
          </motion.div>
        ))}
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <ChartCard
          title="TTS Calls by Day (30d)"
          type="line"
          data={[
            { name: 'Day 1', value: 120 },
            { name: 'Day 2', value: 140 },
            { name: 'Day 3', value: 180 },
            { name: 'Day 4', value: 160 },
            { name: 'Day 5', value: 200 }
          ]}
        />
        <ChartCard
          title="Model Usage Split"
          type="pie"
          data={[
            { name: 'Eleven v3', value: 30 },
            { name: 'Multilingual v2', value: 45 },
            { name: 'Turbo v2.5', value: 15 },
            { name: 'Flash v2.5', value: 10 }
          ]}
        />
      </div>

      {/* Tables */}
      <div className="grid grid-cols-1 gap-6">
        <DataTable
          title="Recent Users"
          columns={recentUsersColumns}
          data={users.slice(0, 5)}
        />
        <DataTable
          title="Recent Jobs (Last 50)"
          columns={recentJobsColumns}
          data={mockJobs}
        />
        <DataTable
          title="Errors (24h)"
          columns={errorsColumns}
          data={mockErrors}
          emptyMessage="No errors in the last 24 hours"
        />
      </div>
    </div>
  );
};

export default Overview;