import React, { useState } from 'react';
import { Routes, Route } from 'react-router-dom';
import Sidebar from './Layout/Sidebar';
import TopBar from './Layout/TopBar';
import Overview from './Pages/Overview';
import Users from './Pages/Users';
import Licences from './Pages/Licences';
import Billing from './Pages/Billing';
import ApiConfiguration from './Pages/ApiConfiguration';
import QuotaLogs from './Pages/QuotaLogs';
import AuditAlerts from './Pages/AuditAlerts';
import Settings from './Pages/Settings';

const AdminDashboard = () => {
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);

  return (
    <div className="min-h-screen bg-app-bg flex">
      <Sidebar collapsed={sidebarCollapsed} onToggle={() => setSidebarCollapsed(!sidebarCollapsed)} />
      
      <div className={`flex-1 transition-all duration-300 ${sidebarCollapsed ? 'ml-16' : 'ml-64'}`}>
        <TopBar />
        
        <main className="p-6">
          <Routes>
            <Route path="/" element={<Overview />} />
            <Route path="/users" element={<Users />} />
            <Route path="/licences" element={<Licences />} />
            <Route path="/billing" element={<Billing />} />
            <Route path="/api-config" element={<ApiConfiguration />} />
            <Route path="/quota-logs" element={<QuotaLogs />} />
            <Route path="/audit-alerts" element={<AuditAlerts />} />
            <Route path="/settings" element={<Settings />} />
          </Routes>
        </main>
      </div>
    </div>
  );
};

export default AdminDashboard;