import React, { createContext, useContext, useState, useEffect } from 'react';
import Cookies from 'js-cookie';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Check for existing auth token
    const token = Cookies.get('auth_token');
    if (token) {
      // In a real app, validate token with backend
      // For now, simulate user data based on stored role
      const storedRole = Cookies.get('user_role') || 'USER';
      const storedEmail = Cookies.get('user_email') || 'demo@voicebaba.ai';
      
      setUser({
        id: 1,
        email: storedEmail,
        role: storedRole,
        enabled: true,
        licence: {
          credits_remaining: 50000,
          expiry: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000), // 30 days from now
          active: true
        }
      });
    }
    setLoading(false);
  }, []);

  const login = async (email, password, isAdmin = false) => {
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Demo account validation
      const validAccounts = {
        'demo@voicebaba.ai': { password: 'demo123', role: 'USER' },
        'admin@voicebaba.ai': { password: 'admin123', role: 'ADMIN' },
        'user@example.com': { password: 'password', role: 'USER' }
      };

      const account = validAccounts[email];
      
      if (!account || account.password !== password) {
        return { success: false, error: 'Invalid email or password' };
      }

      if (isAdmin && account.role !== 'ADMIN') {
        return { success: false, error: 'Admin access required' };
      }

      const mockUser = {
        id: 1,
        email,
        role: account.role,
        enabled: true,
        licence: {
          credits_remaining: account.role === 'ADMIN' ? 999999 : 50000,
          expiry: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000),
          active: true
        }
      };

      setUser(mockUser);
      Cookies.set('auth_token', 'mock_jwt_token', { expires: 7 });
      Cookies.set('user_role', account.role, { expires: 7 });
      Cookies.set('user_email', email, { expires: 7 });
      
      return { success: true };
    } catch (error) {
      return { success: false, error: error.message };
    }
  };

  const logout = () => {
    setUser(null);
    Cookies.remove('auth_token');
    Cookies.remove('user_role');
    Cookies.remove('user_email');
  };

  const value = {
    user,
    login,
    logout,
    loading
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};