import React, { useState } from 'react';
import { motion } from 'framer-motion';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiShield, FiBell, FiMail, FiSlack, FiSave } = FiIcons;

const AuditAlerts = () => {
  const [alertSettings, setAlertSettings] = useState({
    poolLow: { enabled: true, threshold: 10000, email: true, webhook: false },
    keyInvalid: { enabled: true, email: true, webhook: true },
    highErrorRate: { enabled: true, threshold: 5, email: true, webhook: false },
    burstUsage: { enabled: false, threshold: 1000, email: false, webhook: false },
    licenceExpiry: { enabled: true, days: 7, email: true, webhook: false }
  });

  const mockAuditLog = [
    {
      id: 1,
      time: new Date(),
      actor: 'admin@example.com',
      action: 'User Created',
      target: 'user@example.com',
      before: null,
      after: { role: 'USER', enabled: true },
      ip: '192.168.1.1',
      ua: 'Mozilla/5.0...'
    },
    {
      id: 2,
      time: new Date(Date.now() - 3600000),
      actor: 'admin@example.com',
      action: 'Credits Updated',
      target: 'user@example.com',
      before: { credits: 40000 },
      after: { credits: 50000 },
      ip: '192.168.1.1',
      ua: 'Mozilla/5.0...'
    }
  ];

  const handleAlertToggle = (alertType, field, value) => {
    setAlertSettings(prev => ({
      ...prev,
      [alertType]: { ...prev[alertType], [field]: value }
    }));
  };

  const handleSaveSettings = () => {
    // Save alert settings
    console.log('Saving alert settings:', alertSettings);
  };

  return (
    <div className="space-y-6">
      {/* Alert Configuration */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-6 flex items-center">
          <SafeIcon icon={FiBell} className="w-5 h-5 mr-2" />
          Alert Configuration
        </h3>
        
        <div className="space-y-6">
          {/* Pool Low Alert */}
          <div className="border border-gray-600 rounded-lg p-4">
            <div className="flex items-center justify-between mb-4">
              <h4 className="font-medium text-app-text-primary">Pool Low Warning</h4>
              <label className="relative inline-flex items-center cursor-pointer">
                <input
                  type="checkbox"
                  checked={alertSettings.poolLow.enabled}
                  onChange={(e) => handleAlertToggle('poolLow', 'enabled', e.target.checked)}
                  className="sr-only peer"
                />
                <div className="w-11 h-6 bg-gray-600 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-app-primary/20 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-app-primary"></div>
              </label>
            </div>
            
            {alertSettings.poolLow.enabled && (
              <div className="space-y-3">
                <div>
                  <label className="block text-sm text-app-text-primary mb-1">Threshold (credits)</label>
                  <input
                    type="number"
                    value={alertSettings.poolLow.threshold}
                    onChange={(e) => handleAlertToggle('poolLow', 'threshold', parseInt(e.target.value))}
                    className="w-32 px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
                  />
                </div>
                <div className="flex items-center space-x-4">
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={alertSettings.poolLow.email}
                      onChange={(e) => handleAlertToggle('poolLow', 'email', e.target.checked)}
                      className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2 mr-2"
                    />
                    <SafeIcon icon={FiMail} className="w-4 h-4 mr-1" />
                    Email
                  </label>
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={alertSettings.poolLow.webhook}
                      onChange={(e) => handleAlertToggle('poolLow', 'webhook', e.target.checked)}
                      className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2 mr-2"
                    />
                    <SafeIcon icon={FiSlack} className="w-4 h-4 mr-1" />
                    Webhook
                  </label>
                </div>
              </div>
            )}
          </div>

          {/* High Error Rate Alert */}
          <div className="border border-gray-600 rounded-lg p-4">
            <div className="flex items-center justify-between mb-4">
              <h4 className="font-medium text-app-text-primary">High Error Rate</h4>
              <label className="relative inline-flex items-center cursor-pointer">
                <input
                  type="checkbox"
                  checked={alertSettings.highErrorRate.enabled}
                  onChange={(e) => handleAlertToggle('highErrorRate', 'enabled', e.target.checked)}
                  className="sr-only peer"
                />
                <div className="w-11 h-6 bg-gray-600 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-app-primary/20 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-app-primary"></div>
              </label>
            </div>
            
            {alertSettings.highErrorRate.enabled && (
              <div className="space-y-3">
                <div>
                  <label className="block text-sm text-app-text-primary mb-1">Threshold (%)</label>
                  <input
                    type="number"
                    value={alertSettings.highErrorRate.threshold}
                    onChange={(e) => handleAlertToggle('highErrorRate', 'threshold', parseInt(e.target.value))}
                    className="w-32 px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
                  />
                </div>
                <div className="flex items-center space-x-4">
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={alertSettings.highErrorRate.email}
                      onChange={(e) => handleAlertToggle('highErrorRate', 'email', e.target.checked)}
                      className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2 mr-2"
                    />
                    <SafeIcon icon={FiMail} className="w-4 h-4 mr-1" />
                    Email
                  </label>
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={alertSettings.highErrorRate.webhook}
                      onChange={(e) => handleAlertToggle('highErrorRate', 'webhook', e.target.checked)}
                      className="w-4 h-4 text-app-primary bg-gray-600 border-gray-500 rounded focus:ring-app-primary focus:ring-2 mr-2"
                    />
                    <SafeIcon icon={FiSlack} className="w-4 h-4 mr-1" />
                    Webhook
                  </label>
                </div>
              </div>
            )}
          </div>
        </div>

        <div className="mt-6">
          <button
            onClick={handleSaveSettings}
            className="flex items-center px-6 py-2 bg-gradient-to-r from-app-primary to-app-primary-end text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200"
          >
            <SafeIcon icon={FiSave} className="w-4 h-4 mr-2" />
            Save Alert Settings
          </button>
        </div>
      </motion.div>

      {/* Audit Log */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-6 flex items-center">
          <SafeIcon icon={FiShield} className="w-5 h-5 mr-2" />
          Audit Log
        </h3>
        
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Time</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Actor</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Action</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Target</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Changes</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">IP</th>
              </tr>
            </thead>
            <tbody>
              {mockAuditLog.map((entry) => (
                <tr key={entry.id} className="border-b border-gray-700 hover:bg-app-card-deep">
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {entry.time.toLocaleString()}
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{entry.actor}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{entry.action}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{entry.target}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {entry.before && entry.after ? (
                      <div className="text-xs">
                        <div className="text-app-danger">- {JSON.stringify(entry.before)}</div>
                        <div className="text-app-success">+ {JSON.stringify(entry.after)}</div>
                      </div>
                    ) : (
                      <span className="text-app-success">{JSON.stringify(entry.after)}</span>
                    )}
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary font-mono">{entry.ip}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </motion.div>
    </div>
  );
};

export default AuditAlerts;