import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useVoice } from '../../../contexts/VoiceContext';
import SafeIcon from '../../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiX, FiTrash2, FiSave, FiCheck } = FiIcons;

const VoiceLibraryModal = ({ isOpen, onClose }) => {
  const { voiceLibrary, removeVoiceFromLibrary } = useVoice();

  const handleDelete = (id) => {
    removeVoiceFromLibrary(id);
    toast.success('Voice deleted from library');
  };

  const handleSave = (voice) => {
    toast.success('Voice settings saved');
  };

  const handleApply = (voice) => {
    // In real app, this would populate the main form
    toast.success('Voice settings applied to form');
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-50">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            className="bg-app-card rounded-card p-6 w-full max-w-6xl max-h-[80vh] overflow-hidden"
          >
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-bold text-app-text-primary">Voice Library</h2>
              <button
                onClick={onClose}
                className="p-2 text-app-text-secondary hover:text-app-text-primary"
              >
                <SafeIcon icon={FiX} className="w-6 h-6" />
              </button>
            </div>

            <div className="overflow-auto max-h-[60vh]">
              {voiceLibrary.length === 0 ? (
                <div className="text-center py-12 text-app-text-secondary">
                  <p>No voices in library yet</p>
                  <p className="text-sm mt-2">Add voices using the Voice card</p>
                </div>
              ) : (
                <table className="w-full">
                  <thead>
                    <tr className="border-b border-gray-600">
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">ID</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Project</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">ElevenLabs_Id</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Name</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Speed</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Stability</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Similarity</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Style</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Speaker Boost</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Model</th>
                      <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {voiceLibrary.map((voice, index) => (
                      <tr 
                        key={voice.id} 
                        className={`border-b border-gray-700 hover:bg-app-card-deep ${index === 0 ? 'bg-app-primary/10' : ''}`}
                      >
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.id}</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.project}</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.voice_id}</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.name}</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.speed.toFixed(2)}</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.stability}%</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.similarity}%</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.style}%</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.speaker_boost ? 'Yes' : 'No'}</td>
                        <td className="py-3 px-2 text-sm text-app-text-primary">{voice.model}</td>
                        <td className="py-3 px-2">
                          <div className="flex space-x-1">
                            <button
                              onClick={() => handleSave(voice)}
                              className="p-1 text-app-success hover:bg-app-success/20 rounded"
                              title="Save"
                            >
                              <SafeIcon icon={FiSave} className="w-4 h-4" />
                            </button>
                            <button
                              onClick={() => handleApply(voice)}
                              className="p-1 text-app-primary hover:bg-app-primary/20 rounded"
                              title="Apply"
                            >
                              <SafeIcon icon={FiCheck} className="w-4 h-4" />
                            </button>
                            <button
                              onClick={() => handleDelete(voice.id)}
                              className="p-1 text-app-danger hover:bg-app-danger/20 rounded"
                              title="Delete"
                            >
                              <SafeIcon icon={FiTrash2} className="w-4 h-4" />
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}
            </div>

            <div className="flex justify-end mt-6">
              <button
                onClick={onClose}
                className="bg-app-card-deep border border-gray-600 text-app-text-primary py-2 px-6 rounded-full font-semibold hover:bg-gray-600 transition-colors"
              >
                Close
              </button>
            </div>
          </motion.div>
        </div>
      )}
    </AnimatePresence>
  );
};

export default VoiceLibraryModal;