import React, { useState, useRef } from 'react';
import { motion } from 'framer-motion';
import toast from 'react-hot-toast';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiFolder, FiUpload } = FiIcons;

const ProxyCard = () => {
  const [proxyType, setProxyType] = useState('NO');
  const [proxyCount, setProxyCount] = useState(0);
  const [threads, setThreads] = useState(10);
  const fileInputRef = useRef(null);

  const handleFileSelect = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      // Simulate proxy file processing
      const reader = new FileReader();
      reader.onload = (e) => {
        const content = e.target.result;
        const lines = content.split('\n').filter(line => line.trim());
        
        // Simple validation - check if lines look like proxy format
        const validProxies = lines.filter(line => 
          line.includes(':') && line.split(':').length >= 2
        );

        if (validProxies.length === 0) {
          toast.error('No valid proxy found in file');
          setProxyCount(0);
        } else {
          setProxyCount(validProxies.length);
          toast.success(`Loaded ${validProxies.length} proxies`);
        }
      };
      reader.readAsText(file);
    }
  };

  const loadProxies = () => {
    if (proxyCount > 0) {
      toast.success(`${proxyCount} proxies loaded successfully`);
    } else {
      toast.error('No proxies to load');
    }
  };

  const getStatusText = () => {
    if (proxyType === 'NO') return 'No proxy';
    if (proxyCount === 0) return 'No proxies loaded';
    return `Loaded ${proxyCount} proxies`;
  };

  return (
    <motion.div 
      className="bg-app-card rounded-card p-6 shadow-card hover:shadow-card-hover transition-shadow"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.4 }}
    >
      <h3 className="text-lg font-semibold text-app-text-primary mb-4">Proxy</h3>
      
      <div className="space-y-4">
        <div>
          <label className="block text-sm text-app-text-primary mb-2">Type</label>
          <select
            value={proxyType}
            onChange={(e) => setProxyType(e.target.value)}
            className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
          >
            <option value="NO">NO</option>
            <option value="HTTP">HTTP</option>
            <option value="SOCKS">SOCKS</option>
          </select>
        </div>

        <div className="text-sm text-app-text-secondary">
          Status: {getStatusText()}
        </div>

        <div className="flex space-x-2">
          <button
            onClick={handleFileSelect}
            disabled={proxyType === 'NO'}
            className="flex-1 bg-app-blue text-white py-2 px-4 rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
          >
            <SafeIcon icon={FiFolder} className="w-4 h-4 mr-1" />
            Open
          </button>
          <button
            onClick={loadProxies}
            disabled={proxyType === 'NO' || proxyCount === 0}
            className="flex-1 bg-app-teal text-white py-2 px-4 rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
          >
            <SafeIcon icon={FiUpload} className="w-4 h-4 mr-1" />
            Load
          </button>
        </div>

        <div>
          <label className="block text-sm text-app-text-primary mb-2">Thread</label>
          <input
            type="number"
            min="1"
            max="100"
            value={threads}
            onChange={(e) => setThreads(parseInt(e.target.value) || 1)}
            className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
          />
        </div>
      </div>

      <input
        ref={fileInputRef}
        type="file"
        accept=".txt"
        onChange={handleFileChange}
        className="hidden"
      />
    </motion.div>
  );
};

export default ProxyCard;