import React from 'react';
import {HashRouter, Routes, Route, Navigate} from 'react-router-dom';
import {Toaster} from 'react-hot-toast';
import UserPanel from './components/UserPanel/UserPanel';
import AdminDashboard from './components/AdminDashboard/AdminDashboard';
import LoginPage from './components/Auth/LoginPage';
import {AuthProvider} from './contexts/AuthContext';
import {VoiceProvider} from './contexts/VoiceContext';
import {AdminProvider} from './contexts/AdminContext';
import ProtectedRoute from './components/Auth/ProtectedRoute';
import './App.css';

function App() {
  // Check if we're on admin subdomain or admin path
  const isAdminDomain = window.location.hostname.startsWith('admin.') || 
                       window.location.hostname.startsWith('admin--') ||
                       window.location.pathname.startsWith('/admin');

  return (
    <AuthProvider>
      <div className="min-h-screen bg-app-bg text-app-text-primary font-sans">
        <HashRouter>
          <Routes>
            {isAdminDomain ? (
              // Admin Dashboard Routes
              <>
                <Route path="/login" element={<LoginPage isAdmin={true} />} />
                <Route 
                  path="/*" 
                  element={
                    <ProtectedRoute requireAdmin={true}>
                      <AdminProvider>
                        <AdminDashboard />
                      </AdminProvider>
                    </ProtectedRoute>
                  } 
                />
              </>
            ) : (
              // User Panel Routes
              <>
                <Route path="/login" element={<LoginPage isAdmin={false} />} />
                <Route 
                  path="/admin/*" 
                  element={
                    <ProtectedRoute requireAdmin={true}>
                      <AdminProvider>
                        <AdminDashboard />
                      </AdminProvider>
                    </ProtectedRoute>
                  } 
                />
                <Route 
                  path="/" 
                  element={
                    <ProtectedRoute>
                      <VoiceProvider>
                        <UserPanel />
                      </VoiceProvider>
                    </ProtectedRoute>
                  } 
                />
                <Route path="*" element={<Navigate to="/" replace />} />
              </>
            )}
          </Routes>
        </HashRouter>
        
        <Toaster 
          position="top-right"
          toastOptions={{
            duration: 4000,
            style: {
              background: '#182237',
              color: '#E5E7EB',
              border: '1px solid #374151'
            }
          }}
        />
      </div>
    </AuthProvider>
  );
}

export default App;