import React from 'react';
import { useAuth } from '../../contexts/AuthContext';
import SafeIcon from '../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiAlertTriangle } = FiIcons;

const LowCreditBanner = () => {
  const { user } = useAuth();
  const credits = user?.licence?.credits_remaining || 0;

  return (
    <div className="bg-app-danger/20 border-b border-app-danger/30">
      <div className="container mx-auto px-6 py-3">
        <div className="flex items-center justify-center text-app-danger">
          <SafeIcon icon={FiAlertTriangle} className="w-5 h-5 mr-2" />
          <span className="text-sm font-medium">
            {credits <= 0 
              ? 'No credits remaining. Please contact support to top up your account.'
              : `Low credits warning: ${credits.toLocaleString()} credits remaining.`
            }
          </span>
        </div>
      </div>
    </div>
  );
};

export default LowCreditBanner;