import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useVoice } from '../../../contexts/VoiceContext';
import SafeIcon from '../../../common/SafeIcon';
import VoiceLibraryModal from '../Modals/VoiceLibraryModal';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiCheck, FiX, FiLoader, FiDatabase } = FiIcons;

const VoiceCard = () => {
  const { addVoiceToLibrary } = useVoice();
  const [voiceId, setVoiceId] = useState('');
  const [voiceName, setVoiceName] = useState('');
  const [voiceModel, setVoiceModel] = useState('eleven_multilingual_v2');
  const [validationState, setValidationState] = useState('empty'); // empty, checking, valid, invalid
  const [showLibrary, setShowLibrary] = useState(false);

  const validateVoice = async () => {
    if (!voiceId.trim()) {
      setValidationState('empty');
      return;
    }

    setValidationState('checking');
    
    // Simulate API call
    try {
      await new Promise(resolve => setTimeout(resolve, 1500));
      // Mock validation - in real app, call ElevenLabs API
      const isValid = voiceId.length > 5; // Simple mock validation
      
      if (isValid) {
        setValidationState('valid');
        setVoiceName('Valid Voice Name'); // Would come from API
      } else {
        setValidationState('invalid');
        setVoiceName('');
      }
    } catch (error) {
      setValidationState('invalid');
      setVoiceName('');
    }
  };

  const addToLibrary = () => {
    if (validationState !== 'valid') {
      if (!voiceId.trim()) {
        toast.error('Please enter a Voice ID!');
      }
      return;
    }

    const voice = {
      voice_id: voiceId,
      name: voiceName,
      project: 'Default',
      model: voiceModel,
      speed: 1.0,
      stability: 50,
      similarity: 75,
      style: 0,
      speaker_boost: false
    };

    addVoiceToLibrary(voice);
    toast.success('Voice preset added to library!');
  };

  const getValidationDisplay = () => {
    switch (validationState) {
      case 'empty':
        return { text: 'Please enter Voice ID', color: 'text-app-danger' };
      case 'checking':
        return { text: 'Checking...', color: 'text-app-amber' };
      case 'valid':
        return { text: voiceName || 'Valid Voice ID', color: 'text-app-success' };
      case 'invalid':
        return { text: 'Invalid Voice ID', color: 'text-app-danger' };
      default:
        return { text: '', color: '' };
    }
  };

  const validation = getValidationDisplay();

  return (
    <>
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card hover:shadow-card-hover transition-shadow"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <h3 className="text-lg font-semibold text-app-text-primary mb-4">Voice</h3>
        
        <div className="space-y-4">
          <div>
            <input
              type="text"
              placeholder="Enter Voice ID"
              value={voiceId}
              onChange={(e) => {
                setVoiceId(e.target.value);
                setValidationState('empty');
                setVoiceName('');
              }}
              className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary placeholder-app-text-secondary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
            />
            {voiceId && (
              <div className={`text-sm mt-1 flex items-center ${validation.color}`}>
                {validationState === 'checking' && (
                  <SafeIcon icon={FiLoader} className="w-4 h-4 mr-1 animate-spin" />
                )}
                {validationState === 'valid' && (
                  <SafeIcon icon={FiCheck} className="w-4 h-4 mr-1" />
                )}
                {validationState === 'invalid' && (
                  <SafeIcon icon={FiX} className="w-4 h-4 mr-1" />
                )}
                {validation.text}
              </div>
            )}
          </div>

          <div>
            <select
              value={voiceModel}
              onChange={(e) => setVoiceModel(e.target.value)}
              className="w-full px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
            >
              <option value="eleven_multilingual_v2">Eleven Multilingual v2</option>
              <option value="eleven_v3">Eleven v3</option>
              <option value="eleven_turbo_v2_5">Eleven Turbo v2.5</option>
              <option value="eleven_flash_v2_5">Eleven Flash v2.5</option>
            </select>
          </div>

          <div className="flex space-x-2">
            <button
              onClick={validateVoice}
              disabled={validationState === 'checking'}
              className="flex-1 bg-gradient-to-r from-app-primary to-app-primary-end text-white py-2 px-4 rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50"
            >
              Validate
            </button>
            <button
              onClick={addToLibrary}
              disabled={validationState !== 'valid'}
              className="flex-1 bg-app-success text-white py-2 px-4 rounded-full text-sm font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Add to Library
            </button>
          </div>

          <button
            onClick={() => setShowLibrary(true)}
            className="w-full bg-app-card-deep border border-gray-600 text-app-text-primary py-2 px-4 rounded-full text-sm font-semibold hover:bg-gray-600 transition-colors flex items-center justify-center"
          >
            <SafeIcon icon={FiDatabase} className="w-4 h-4 mr-2" />
            Voice Library
          </button>
        </div>
      </motion.div>

      <VoiceLibraryModal 
        isOpen={showLibrary} 
        onClose={() => setShowLibrary(false)}
      />
    </>
  );
};

export default VoiceCard;