import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useAdmin } from '../../../contexts/AdminContext';
import SafeIcon from '../../../common/SafeIcon';
import toast from 'react-hot-toast';
import * as FiIcons from 'react-icons/fi';

const { FiFilter, FiEdit, FiPlus, FiMinus, FiClock, FiXCircle, FiRefreshCw } = FiIcons;

const Licences = () => {
  const { users, updateUser } = useAdmin();
  const [filterActive, setFilterActive] = useState('all');
  const [editingLicence, setEditingLicence] = useState(null);

  const filteredUsers = users.filter(user => {
    if (filterActive === 'active') return user.enabled && new Date(user.expiry) > new Date();
    if (filterActive === 'expired') return new Date(user.expiry) <= new Date();
    return true;
  });

  const updateCredits = (userId, change) => {
    const user = users.find(u => u.id === userId);
    const newCredits = Math.max(0, user.credits_remaining + change);
    updateUser(userId, { credits_remaining: newCredits });
    toast.success(`Credits ${change > 0 ? 'added' : 'deducted'}`);
  };

  const extendLicence = (userId, days) => {
    const user = users.find(u => u.id === userId);
    const newExpiry = new Date(user.expiry);
    newExpiry.setDate(newExpiry.getDate() + days);
    updateUser(userId, { expiry: newExpiry });
    toast.success(`Licence extended by ${days} days`);
  };

  return (
    <div className="space-y-6">
      {/* Filters */}
      <div className="bg-app-card rounded-card p-6 shadow-card">
        <div className="flex items-center space-x-4">
          <SafeIcon icon={FiFilter} className="w-5 h-5 text-app-text-secondary" />
          <div className="flex space-x-2">
            {[
              { key: 'all', label: 'All' },
              { key: 'active', label: 'Active' },
              { key: 'expired', label: 'Expired' }
            ].map(filter => (
              <button
                key={filter.key}
                onClick={() => setFilterActive(filter.key)}
                className={`px-4 py-2 rounded-full text-sm font-medium transition-colors ${
                  filterActive === filter.key
                    ? 'bg-app-primary text-white'
                    : 'bg-app-card-deep text-app-text-secondary hover:text-app-text-primary'
                }`}
              >
                {filter.label}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Licences Table */}
      <div className="bg-app-card rounded-card p-6 shadow-card">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">User</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Credits</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Expiry</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Active</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Updated</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredUsers.map((user) => {
                const isExpired = new Date(user.expiry) <= new Date();
                const daysUntilExpiry = Math.ceil((new Date(user.expiry) - new Date()) / (1000 * 60 * 60 * 24));
                
                return (
                  <motion.tr 
                    key={user.id}
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    className="border-b border-gray-700 hover:bg-app-card-deep"
                  >
                    <td className="py-3 px-2 text-sm text-app-text-primary">{user.email}</td>
                    <td className="py-3 px-2 text-sm text-app-text-primary">
                      {user.credits_remaining?.toLocaleString()}
                    </td>
                    <td className="py-3 px-2 text-sm">
                      <div className="flex items-center">
                        <span className={`${isExpired ? 'text-app-danger' : daysUntilExpiry <= 7 ? 'text-app-amber' : 'text-app-text-primary'}`}>
                          {new Date(user.expiry).toLocaleDateString()}
                        </span>
                        {!isExpired && daysUntilExpiry <= 7 && (
                          <span className="ml-2 text-xs text-app-amber">
                            ({daysUntilExpiry}d left)
                          </span>
                        )}
                      </div>
                    </td>
                    <td className="py-3 px-2 text-sm">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        user.enabled && !isExpired 
                          ? 'bg-app-success/20 text-app-success' 
                          : 'bg-app-danger/20 text-app-danger'
                      }`}>
                        {user.enabled && !isExpired ? 'Active' : 'Inactive'}
                      </span>
                    </td>
                    <td className="py-3 px-2 text-sm text-app-text-primary">
                      {new Date().toLocaleDateString()}
                    </td>
                    <td className="py-3 px-2">
                      <div className="flex items-center space-x-1">
                        <button
                          onClick={() => updateCredits(user.id, 10000)}
                          className="p-1 text-app-success hover:bg-app-success/20 rounded"
                          title="Add 10K Credits"
                        >
                          <SafeIcon icon={FiPlus} className="w-4 h-4" />
                        </button>
                        <button
                          onClick={() => updateCredits(user.id, -10000)}
                          className="p-1 text-app-danger hover:bg-app-danger/20 rounded"
                          title="Deduct 10K Credits"
                        >
                          <SafeIcon icon={FiMinus} className="w-4 h-4" />
                        </button>
                        <button
                          onClick={() => extendLicence(user.id, 30)}
                          className="p-1 text-app-primary hover:bg-app-primary/20 rounded"
                          title="Extend 30 Days"
                        >
                          <SafeIcon icon={FiClock} className="w-4 h-4" />
                        </button>
                        <button
                          onClick={() => updateUser(user.id, { enabled: false })}
                          className="p-1 text-app-amber hover:bg-app-amber/20 rounded"
                          title="Revoke"
                        >
                          <SafeIcon icon={FiXCircle} className="w-4 h-4" />
                        </button>
                        <button
                          onClick={() => {
                            const newExpiry = new Date();
                            newExpiry.setFullYear(newExpiry.getFullYear() + 1);
                            updateUser(user.id, { 
                              enabled: true, 
                              expiry: newExpiry,
                              credits_remaining: 100000 
                            });
                            toast.success('Licence renewed');
                          }}
                          className="p-1 text-app-teal hover:bg-app-teal/20 rounded"
                          title="Renew"
                        >
                          <SafeIcon icon={FiRefreshCw} className="w-4 h-4" />
                        </button>
                      </div>
                    </td>
                  </motion.tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Licences;