import React, { createContext, useContext, useState } from 'react';

const VoiceContext = createContext();

export const useVoice = () => {
  const context = useContext(VoiceContext);
  if (!context) {
    throw new Error('useVoice must be used within a VoiceProvider');
  }
  return context;
};

export const VoiceProvider = ({ children }) => {
  const [voiceLibrary, setVoiceLibrary] = useState([]);
  const [subtitles, setSubtitles] = useState([]);
  const [isProcessing, setIsProcessing] = useState(false);
  const [processedCount, setProcessedCount] = useState(0);
  const [processingCount, setProcessingCount] = useState(0);
  const [elapsedTime, setElapsedTime] = useState(0);

  const addVoiceToLibrary = (voice) => {
    const newVoice = {
      id: Date.now(),
      ...voice,
      created_at: new Date()
    };
    setVoiceLibrary(prev => [newVoice, ...prev]);
    return newVoice;
  };

  const removeVoiceFromLibrary = (id) => {
    setVoiceLibrary(prev => prev.filter(voice => voice.id !== id));
  };

  const addSubtitles = (newSubtitles) => {
    setSubtitles(prev => [...prev, ...newSubtitles]);
  };

  const clearSubtitles = () => {
    setSubtitles([]);
    setProcessedCount(0);
    setProcessingCount(0);
    setElapsedTime(0);
  };

  const startProcessing = () => {
    setIsProcessing(true);
    setElapsedTime(0);
  };

  const stopProcessing = () => {
    setIsProcessing(false);
  };

  const value = {
    voiceLibrary,
    subtitles,
    isProcessing,
    processedCount,
    processingCount,
    elapsedTime,
    addVoiceToLibrary,
    removeVoiceFromLibrary,
    addSubtitles,
    clearSubtitles,
    startProcessing,
    stopProcessing,
    setSubtitles,
    setProcessedCount,
    setProcessingCount,
    setElapsedTime
  };

  return (
    <VoiceContext.Provider value={value}>
      {children}
    </VoiceContext.Provider>
  );
};