import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useAdmin } from '../../../contexts/AdminContext';
import ChartCard from '../Components/ChartCard';
import SafeIcon from '../../../common/SafeIcon';
import * as FiIcons from 'react-icons/fi';

const { FiDownload, FiFilter, FiSearch } = FiIcons;

const QuotaLogs = () => {
  const { globalPool } = useAdmin();
  const [dateRange, setDateRange] = useState('24h');
  const [statusFilter, setStatusFilter] = useState('all');

  const mockLogs = [
    {
      id: 1,
      time: new Date(),
      user: 'user@example.com',
      endpoint: '/v1/text-to-speech',
      status: 200,
      latency: 1250,
      bytes: 15420,
      trace_id: 'tr_abc123'
    },
    {
      id: 2,
      time: new Date(Date.now() - 3600000),
      user: 'user2@example.com',
      endpoint: '/v1/voices',
      status: 200,
      latency: 320,
      bytes: 2140,
      trace_id: 'tr_def456'
    }
  ];

  const usageData = [
    { name: '00:00', calls: 45, responseTime: 1200 },
    { name: '04:00', calls: 32, responseTime: 1100 },
    { name: '08:00', calls: 78, responseTime: 1350 },
    { name: '12:00', calls: 95, responseTime: 1450 },
    { name: '16:00', calls: 88, responseTime: 1300 },
    { name: '20:00', calls: 67, responseTime: 1250 }
  ];

  const errorData = [
    { name: '200', value: 85 },
    { name: '400', value: 10 },
    { name: '429', value: 3 },
    { name: '500', value: 2 }
  ];

  const getStatusColor = (status) => {
    if (status >= 200 && status < 300) return 'text-app-success';
    if (status >= 400 && status < 500) return 'text-app-amber';
    return 'text-app-danger';
  };

  return (
    <div className="space-y-6">
      {/* Usage Summary */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <motion.div 
          className="bg-app-card rounded-card p-6 shadow-card"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
        >
          <div className="text-center">
            <p className="text-sm text-app-text-secondary mb-1">Pool Total</p>
            <p className="text-2xl font-bold text-app-text-primary">{globalPool.total_pool.toLocaleString()}</p>
          </div>
        </motion.div>
        
        <motion.div 
          className="bg-app-card rounded-card p-6 shadow-card"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
        >
          <div className="text-center">
            <p className="text-sm text-app-text-secondary mb-1">Allocated</p>
            <p className="text-2xl font-bold text-app-primary">{globalPool.allocated.toLocaleString()}</p>
          </div>
        </motion.div>
        
        <motion.div 
          className="bg-app-card rounded-card p-6 shadow-card"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
        >
          <div className="text-center">
            <p className="text-sm text-app-text-secondary mb-1">Calls (24h)</p>
            <p className="text-2xl font-bold text-app-text-primary">1,247</p>
          </div>
        </motion.div>
        
        <motion.div 
          className="bg-app-card rounded-card p-6 shadow-card"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
        >
          <div className="text-center">
            <p className="text-sm text-app-text-secondary mb-1">Avg Response</p>
            <p className="text-2xl font-bold text-app-text-primary">1.2s</p>
          </div>
        </motion.div>
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <ChartCard
          title="Calls per Hour (24h)"
          type="line"
          data={usageData.map(d => ({ name: d.name, value: d.calls }))}
        />
        <ChartCard
          title="Response Codes Distribution"
          type="pie"
          data={errorData}
        />
      </div>

      {/* Logs Table */}
      <motion.div 
        className="bg-app-card rounded-card p-6 shadow-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4 }}
      >
        <div className="flex items-center justify-between mb-6">
          <h3 className="text-lg font-semibold text-app-text-primary">Request Logs</h3>
          
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <select
                value={dateRange}
                onChange={(e) => setDateRange(e.target.value)}
                className="px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
              >
                <option value="1h">Last Hour</option>
                <option value="24h">Last 24 Hours</option>
                <option value="7d">Last 7 Days</option>
                <option value="30d">Last 30 Days</option>
              </select>
              
              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                className="px-3 py-2 bg-app-card-deep border border-gray-600 rounded-lg text-app-text-primary focus:outline-none focus:ring-2 focus:ring-app-primary focus:border-transparent"
              >
                <option value="all">All Status</option>
                <option value="2xx">Success (2xx)</option>
                <option value="4xx">Client Error (4xx)</option>
                <option value="5xx">Server Error (5xx)</option>
              </select>
            </div>
            
            <button className="flex items-center px-4 py-2 bg-app-primary text-white rounded-full font-semibold hover:shadow-lg transform hover:scale-[1.02] transition-all duration-200">
              <SafeIcon icon={FiDownload} className="w-4 h-4 mr-2" />
              Export CSV
            </button>
          </div>
        </div>
        
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-gray-600">
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Time</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">User</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Endpoint</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Status</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Latency</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Bytes</th>
                <th className="text-left py-3 px-2 text-sm font-medium text-app-text-primary">Trace ID</th>
              </tr>
            </thead>
            <tbody>
              {mockLogs.map((log) => (
                <tr key={log.id} className="border-b border-gray-700 hover:bg-app-card-deep">
                  <td className="py-3 px-2 text-sm text-app-text-primary">
                    {log.time.toLocaleTimeString()}
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{log.user}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary font-mono">{log.endpoint}</td>
                  <td className="py-3 px-2 text-sm">
                    <span className={`font-medium ${getStatusColor(log.status)}`}>
                      {log.status}
                    </span>
                  </td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{log.latency}ms</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary">{log.bytes.toLocaleString()}</td>
                  <td className="py-3 px-2 text-sm text-app-text-primary font-mono">{log.trace_id}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </motion.div>
    </div>
  );
};

export default QuotaLogs;